html {
  font-size: 100%;
  box-sizing: border-box;
  max-width: 100%;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--text-color);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-light: hsl(230, 19%, 60%);
  --toggle-button-light: hsl(228, 46%, 96%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-bg2: hsl(220, 6%, 10%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --dark-accent-color1: hsl(324, 100%, 46%);
  --light-bg: hsl(0, 0%, 100%);
  --light-bg2: hsl(44, 89%, 93%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-text1: hsl(230, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --light-accent-color1: ;
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
  --navbar-dark-gradient: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0.7) 25%,
      rgba(3, 2, 2, 0.4) 50%,
      rgba(0, 0, 0, 0.2) 80%,
      rgba(0, 0, 0, 0.01) 100%
  );
  --navbar-light-gradient: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0.7) 25%,
      rgba(255, 255, 255, 0.4) 50%,
      rgba(255, 255, 255, 0.2) 80%,
      rgba(255, 255, 255, 0.01) 100%
  );
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --text-color: var(--dark-text2);
    --text-color2: var(--dark-text1);
    --card-bg: var(--dark-card);
    --card-hover: var(--dark-card-hover);
    --toggle: var(--light-bg);
    --toggle-bg: linear-gradient(
        225deg,
        var(--toggle-bg-end) 0%,
        var(--toggle-bg-start) 98.02%
    );
    --toggle-button: var(--dark-bg);
    --navbar-gradient: var(--navbar-dark-gradient);
    --accent-color1: var(--dark-accent-color1);
  }
}
body.light {
  --accent-color1: var(--light-accent-color1);
  --background: var(--light-bg);
  --background2: var(--light-bg2);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
  --navbar-gradient: var(--navbar-light-gradient);
}

body.dark {
  --accent-color1: var(--dark-accent-color1);
  --background: var(--dark-bg);
  --background2: var(--dark-bg2);
  --text-color: var(--dark-text2);
  --text-color2: var(--dark-text1);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --toggle: var(--light-bg);
  --toggle-bg: linear-gradient(
      225deg,
      var(--toggle-bg-end) 0%,
      var(--toggle-bg-start) 98.02%
  );
  --toggle-button: var(--dark-bg);
  --navbar-gradient: var(--navbar-dark-gradient);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875) {
  .container {
    padding: 0;
  }
}

.wrapper {
  max-width: 100vw;
  padding: 0;
  margin: 0;
}
@media (max-width: 39.9375em) {
  .wrapper {
    display: none;
  }
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: var(--background2);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--text-color);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--text-color2);
}

h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  font-size: 1.5rem;
}
@media (min-width: 71.875) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  color: inherit;
  text-decoration: none;
}

.card {
  padding: 1.25rem;
}

.header {
  display: flex;
  flex-wrap: wrap;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  transition: 0.6s;
  padding: 40px 100px;
  background: var(--navbar-gradient);
  z-index: 100;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color2);
}

.header:hover {
  background: var(--background);
  transition: 0s;
}

.sticky {
  padding: 5px 100px;
  background: var(--background);
}

.navigation {
  display: flex;
}
.navigation__items {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.navigation__link {
  position: relative;
  list-style: none;
}
.navigation__link a {
  position: relative;
  margin: 0 15px;
  text-decoration: none;
  color: var(--text-color1);
  letter-spacing: 2px;
  font-weight: 400px;
  transition: 0.6;
}

.section__wrapper {
  max-width: 100%;
  height: 1000px;
}

.footer {
  padding-top: 64px;
  padding-bottom: 64px;
  height: 60vh;
  background: var(--background2);
}
.footer__wrapper {
  max-width: 1230px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
}
.footer__row1 {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 65%;
}
.footer__row1__column1 {
  display: flex;
  flex-direction: column;
  width: 50%;
  height: 100%;
}
.footer__row1__column1 form .form__group {
  position: relative;
  padding: 15px 0 0;
  margin-top: 10px;
  width: 100%;
}
.footer__row1__column1 form .form__group .form__field {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 1px solid var(--text-color);
  outline: 0;
  font-size: 1.3rem;
  color: var(--text-color);
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
}
.footer__row1__column1 form .form__group .form__field::placeholder {
  color: transparent;
}
.footer__row1__column1 form .form__group .form__field:placeholder-shown ~ .form__label {
  font-size: 1.3rem;
  cursor: text;
  top: 20px;
}
.footer__row1__column1 form .form__group .form__field:focus {
  font-weight: 700;
  border-width: 1px;
  border-image: linear-gradient(to right, var(--text-color, var(--text-color2)));
  border-image-slice: 1;
}
.footer__row1__column1 form .form__group .form__field:focus ~ .form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: var(--text-color);
  font-weight: 700;
}
.footer__row1__column1 form .form__group .form__field:required, .footer__row1__column1 form .form__group .form__field:invalid {
  box-shadow: none;
}
.footer__row1__column1 form .form__group .fa-paper-plane {
  background: border-box;
  background: transparent;
  border: none;
  position: absolute;
  right: 0;
  font-size: 1.5625rem;
  color: var(--accent-color1);
}
.footer__row1__column1 form .form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: var(--text-color2);
}
.footer__row1__column1 form .form__submit {
  display: block;
  margin: auto;
  width: 75%;
  margin-top: 50px;
  padding: 0.625rem;
  background-color: var(--text-color);
  color: var(--background);
  border: 2px solid var(--text-color);
}
.footer__row1__column1 form .form__submit:hover {
  background-color: var(--background);
  color: var(--text-color);
}
.footer__row1__column2 {
  display: flex;
  width: 50%;
  height: 100%;
}
.footer__row2 {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 25%;
  margin-top: 50px;
}
.footer__row2__column1 {
  display: flex;
  flex-direction: column;
  width: 40%;
}
.footer__row2__column1 .footer__logo {
  padding: 0;
}
.footer__row2__column2 {
  display: flex;
  flex-direction: column;
  width: 15%;
}
.footer__row2__column2 .footer__about {
  padding: 0;
  margin: 0;
}
.footer__row2__column2 .footer__about li {
  list-style: none;
  margin: 2% 0 2% 0;
}
.footer__row2__column3 {
  display: flex;
  flex-direction: column;
  width: 20%;
  height: 100%;
}
.footer__row2__column3 .footer__terms-privacy {
  padding: 0;
  margin: 0;
}
.footer__row2__column3 .footer__terms-privacy li {
  list-style: none;
}
.footer__row2__column4 {
  display: flex;
  flex-direction: column;
  width: 20%;
  text-align: center;
}
.footer__row2__column4 .footer__socials {
  font-size: 2.1875rem;
  display: inline-flex;
  align-items: center;
  padding: 0;
}
.footer__row2__column4 .footer__socials li {
  list-style: none;
  margin: auto;
}
.footer__row2__column4 .footer__socials li .fa-facebook {
  color: var(--facebook);
}
.footer__row2__column4 .footer__socials li .fa-instagram {
  display: inline-block;
  width: 32px;
  height: 32px;
  text-align: center;
  border-radius: 4px;
  color: #fff;
  line-height: 34px;
  vertical-align: middle;
  background: #d6249f;
  background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.25);
}
.footer__row2__column4 .footer__socials li .fa-twitter {
  color: var(--twitter);
}
.footer__row2__column4 .footer__socials li .fa-linkedin {
  color: var(--linkedin);
}
.footer__copyright {
  height: 10%;
  border-top: 1px solid var(--text-color);
  text-align: center;
  margin: auto;
  align-items: center;
  justify-content: center;
  display: flex;
}
.footer__copyright h6 {
  margin: 20px 0 0 0;
  padding: 0;
}

.slideshow__container {
  width: 100vw;
  margin: 0 auto;
}
.slideshow__carousel {
  height: 70vh;
  overflow: hidden;
  position: relative;
}
.slideshow__slider {
  display: flex;
  height: 100%;
  width: 300%;
}
.slideshow__slider section {
  position: absolute;
}
.slideshow__image {
  opacity: 1;
  transition: fadeIn;
  -webkit-animation: fadeIn 2s ease-in;
  /* Safari 4+ */
  -moz-animation: fadeIn 2s ease-in;
  /* Fx 5+ */
  -o-animation: fadeIn 2s ease-in;
  /* Opera 12+ */
  animation: fadeIn 2s ease-in;
  /* IE 10+, Fx 29+ */
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.slideshow__controller {
  z-index: 10;
}
.slideshow__controller ul {
  list-style: none;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 50%;
  margin: 0;
  padding: 0;
  transform: translate(-50%);
  z-index: inherit;
}
.slideshow__controller ul li {
  width: 10px;
  height: 10px;
  background: var(--light-bg);
  border: 2px solid var(--light-bg);
  border-radius: 50px;
  margin: 10px;
  cursor: pointer;
}
.slideshow__selected {
  background: transparent !important;
}

.sliderarrow-next,
.sliderarrow-prev {
  z-index: 10;
  cursor: pointer;
  height: 120px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: transform 0.1s;
  width: 120px;
}
.sliderarrow-next__top, .sliderarrow-next__bot,
.sliderarrow-prev__top,
.sliderarrow-prev__bot {
  background-color: var(--background);
  height: 4px;
  position: absolute;
  top: 50%;
  width: 100%;
}
.sliderarrow-next__top:after, .sliderarrow-next__bot:after,
.sliderarrow-prev__top:after,
.sliderarrow-prev__bot:after {
  background-color: var(--text-color);
  content: "";
  height: 100%;
  position: absolute;
  top: 0;
  transition: all 0.15s;
}

.sliderarrow-next {
  right: 3%;
  padding: 250px 150px 150px 0px;
}
.sliderarrow-next__top {
  transform: rotate(45deg);
  transform-origin: bottom right;
}
.sliderarrow-next__top:after {
  left: 100%;
  right: 0;
  transition-delay: 0s;
}
.sliderarrow-next__bot {
  transform: rotate(-45deg);
  transform-origin: top right;
}
.sliderarrow-next__bot:after {
  left: 0;
  right: 100%;
  transition-delay: 0.15s;
}
.sliderarrow-next:hover .sliderarrow-next__top:after {
  left: 0;
  transition-delay: 0.15s;
}
.sliderarrow-next:hover .sliderarrow-next__bot:after {
  right: 0;
  transition-delay: 0s;
}
.sliderarrow-next:active {
  transform: translateX(-10%) translateY(-50%) scale(0.9);
}

.sliderarrow-prev {
  left: 3%;
  padding: 250px 150px 150px 0px;
}
.sliderarrow-prev__top {
  transform: rotate(-45deg);
  transform-origin: bottom left;
}
.sliderarrow-prev__top:after {
  right: 100%;
  left: 0;
  transition-delay: 0s;
}
.sliderarrow-prev__bot {
  transform: rotate(45deg);
  transform-origin: top left;
}
.sliderarrow-prev__bot:after {
  right: 0;
  left: 100%;
  transition-delay: 0.15s;
}
.sliderarrow-prev:hover .sliderarrow-prev__top:after {
  right: 0;
  transition-delay: 0.15s;
}
.sliderarrow-prev:hover .sliderarrow-prev__bot:after {
  left: 0;
  transition-delay: 0s;
}
.sliderarrow-prev:active {
  transform: translateX(10%) translateY(-50%) scale(0.9);
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  border: none;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--toggle);
}
.toggle label[for=dark] {
  line-height: 1.5rem;
  margin-right: 0.8125rem;
}
.toggle__wrapper {
  position: relative;
  height: 1.5rem;
}
.toggle input[type=radio] {
  margin: 0 -0.125rem 0 -0.125rem;
  opacity: 0;
  width: 1.5rem;
  height: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid white;
}
.toggle__background {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  background: var(--toggle-bg);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-button);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}