@use '../util' as *;

.section {
    &__wrapper {
        max-width: 100%;
        height: 1000px;
    }
}

.footer {
    padding-top: 64px;
    padding-bottom: 64px;
    height: 60vh;
    background: var(--background2);
    &__wrapper {
        max-width: 1230px;
        width: 100%;
        margin-right: auto;
        margin-left: auto;
        padding-right: 15px;
        padding-left: 15px;
    }
    &__row1 {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        height: 65%;
        &__column1 {
            display: flex;
            flex-direction: column;
            width: 50%;
            height: 100%;
            form {
                .form__group {
                    position: relative;
                    padding: 15px 0 0;
                    margin-top: 10px;
                    width: 100%;
                    .form__field {
                        font-family: inherit;
                        width: 100%;
                        border: 0;
                        border-bottom: 1px solid var(--text-color);
                        outline: 0;
                        font-size: 1.3rem;
                        color: var(--text-color);
                        padding: 7px 0;
                        background: transparent;
                        transition: border-color 0.2s;
                        &::placeholder {
                            color: transparent;
                        }
                        &:placeholder-shown ~ .form__label {
                            font-size: 1.3rem;
                            cursor: text;
                            top: 20px;
                        }
                    }
                    .form__field:focus {
                        ~ .form__label {
                            position: absolute;
                            top: 0;
                            display: block;
                            transition: 0.2s;
                            font-size: 1rem;
                            color: var(--text-color);
                            font-weight: 700;
                        }
                        font-weight: 700;
                        border-width: 1px;
                        border-image: linear-gradient(to right, var(--text-color, var(--text-color2)));
                        border-image-slice: 1;
                    }
                    .form__field{
                        &:required,
                        &:invalid { 
                            box-shadow:none;
                        }
                    }
                    .fa-paper-plane {
                        background: border-box;
                        background: transparent;
                        border: none;
                        position: absolute;
                        right: 0;
                        font-size: rem(25);
                        color: var(--accent-color1);
                    }
                }
                .form__label {
                    position: absolute;
                    top: 0;
                    display: block;
                    transition: 0.2s;
                    font-size: 1rem;
                    color: var(--text-color2);
                }
                .form__submit {
                    display: block;
                    margin: auto;
                    width: 75%;
                    margin-top: 50px;
                    padding: rem(10);
                    background-color: var(--text-color);
                    color: var(--background);
                    border: 2px solid var(--text-color);
                }
                .form__submit:hover {
                    background-color: var(--background);
                    color: var(--text-color);
                }
            }
        }
        &__column2 {
            display: flex;
            width: 50%;
            height: 100%;
        }
    }
    &__row2 {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        height: 25%;
        margin-top: 50px;
        &__column1 {
            display: flex;
            flex-direction: column;
            width: 40%;
            .footer__logo {
                padding: 0;
            }
        }
        &__column2 {
            display: flex;
            flex-direction: column;
            width: 15%;
            .footer__about {
                padding: 0;
                margin: 0;
                li {
                    list-style: none;
                    margin: 2% 0 2% 0;
                }
            }
        }
        &__column3 {
            display: flex;
            flex-direction: column;
            width: 20%;
            height: 100%;
            .footer__terms-privacy {
                padding: 0;
                margin: 0;
                li {
                    list-style: none;
                }
            }
        }
        &__column4 {
            display: flex;
            flex-direction: column;
            width: 20%;
            text-align: center;
            .footer__socials {
                font-size: rem(35);
                display: inline-flex;
                align-items: center;
                padding: 0;
                // margin: 0px 5% 0 5%;
                li {
                    list-style: none;
                    margin: auto;
                    .fa-facebook { color: var(--facebook); }
                    .fa-instagram {
                        display: inline-block;
                        width: 32px;
                        height: 32px;
                        text-align: center;
                        border-radius: 4px;
                        color: #fff;
                        line-height: 34px;
                        vertical-align: middle;
                        background: #d6249f;
                        background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
                        box-shadow: 0px 3px 10px rgba(0,0,0,.25);
                    }
                    .fa-twitter { color: var(--twitter); }
                    .fa-linkedin { color: var(--linkedin); }
                }
            }
        }
    }
    &__copyright {
        height: 10%;
        border-top: 1px solid var(--text-color);
        text-align: center;
        margin: auto;
        align-items: center;
        justify-content: center;
        display: flex;
        h6 {
            margin: 20px 0 0 0;
            padding: 0;
        }
    }
}
