@use '../util' as *;

.slideshow {
    &__container {
        width: 100vw;
        margin: 0 auto;
    }
    &__carousel {
        height: 70vh;
        overflow: hidden;
        position: relative;
    }
    &__slider {
        display: flex;
        height: 100%;
        width: 300%;
        section {
            position: absolute;
        }
    }
    &__image {
        opacity: 1;
        transition: fadeIn;
        -webkit-animation: fadeIn 2s ease-in; /* Safari 4+ */
        -moz-animation:    fadeIn 2s ease-in; /* Fx 5+ */
        -o-animation:      fadeIn 2s ease-in; /* Opera 12+ */
        animation:         fadeIn 2s ease-in; /* IE 10+, Fx 29+ */
    }
}
@keyframes fadeIn {
    0%   { opacity: 0; }
    100% { opacity: 1; }
}

.slideshow {
    &__controller {
        z-index: 10;
        ul {
            list-style: none;
            display: flex;
            position: absolute;
            bottom: 0;
            left: 50%;
            margin: 0;
            padding: 0;
            transform: translate(-50%);
            z-index: inherit;
            li {
                width: 10px;
                height: 10px;
                background: var(--light-bg);
                border: 2px solid var(--light-bg);
                border-radius: 50px;
                margin: 10px;
                cursor: pointer;
            }
        }
    }
    &__selected {
        background: transparent !important;
    }
}

.sliderarrow-next,
.sliderarrow-prev {
    z-index: 10;
    cursor: pointer;
    height: 120px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transition: transform .1s;
    width: 120px;
    &__top,
    &__bot {
        background-color: var(--background);
        height: 4px;
        //left: -5px;
        position: absolute;
        top: 50%;
        width: 100%;
        &:after {
            background-color: var(--text-color);
            content: '';
            height: 100%;
            position: absolute;
            top: 0;
            transition: all .15s;
        }
    }
}
.sliderarrow-next {
    right: 3%;
    padding: 250px 150px 150px 0px;
    &__top {
        transform: rotate(45deg);
        transform-origin: bottom right;
        &:after {
            left: 100%;
            right: 0;
            transition-delay: 0s;
        }
    }
    &__bot {
        transform: rotate(-45deg);
        transform-origin: top right;
        &:after {
            left: 0;
            right: 100%;
            transition-delay: .15s;
        }
    }
    &:hover & {
        &__top:after {
            left: 0;
            transition-delay: .15s;
        }
        &__bot:after {
            right: 0;
            transition-delay: 0s;
        }
    }
    &:active {
        transform: translateX(-10%) translateY(-50%) scale(0.9);
    }
}
.sliderarrow-prev {
    left: 3%;
    padding: 250px 150px 150px 0px;
    &__top {
        transform: rotate(-45deg);
        transform-origin: bottom left;
        &:after {
            right: 100%;
            left: 0;
            transition-delay: 0s;
        }
    }
    &__bot {
        transform: rotate(45deg);
        transform-origin: top left;
        &:after {
            right: 0;
            left: 100%;
            transition-delay: .15s;
        }
    }
    &:hover & {
        &__top:after {
            right: 0;
            transition-delay: .15s;
        }
        &__bot:after {
            left: 0;
            transition-delay: 0s;
        }
    }
    &:active {
        transform: translateX(10%) translateY(-50%) scale(0.9);
    }
}